<template>
  <div style="padding: 10px">
    <div style="margin: 10px 0 ;display: flex">
<!--      <el-button type="primary">导入</el-button>-->
<!--      <el-button type="primary">导出</el-button>-->
      <el-button type="primary" @click="handleEdit2">新增厂商</el-button>
    </div>
    <!--    <div style="margin: 10px 0 ;display: flex">-->
    <!--      <el-input v-model="search" placeholder="请输入RFID" style="width: 20%" clearable/>-->
    <!--      <el-button type="primary" style="margin-left: 5px">查询</el-button>-->
    <!--    </div>-->
    <el-table :data="tableData" stripe style="width: 100%" border>
      <el-table-column prop="id" label="ID" width="150" sortable/>
      <el-table-column prop="name" label="厂商名称" width="150" />
      <el-table-column prop="telephone" label="厂商电话" width="150" />
      <el-table-column prop="companyAddress" label="厂商地址" width="150" />
      <el-table-column prop="factoryAddress" label="工厂地址" width="150" />
      <el-table-column prop="web" label="厂商网站" width="150" />
      <el-table-column prop="legalrepresentative" label="法人代表" width="150" />
      <!--     <el-table-column prop="nickName" label="昵称" width="150" />-->
      <!--     <el-table-column prop="age" label="年龄" width="150" />-->
      <!--     <el-table-column prop="sex" label="性别" width="150" />-->
      <!--     <el-table-column prop="address" label="地址" width="300" />-->
      <el-table-column fixed="right" label="操作" width="170">
        <template #default="scope">
          <el-button type="text" size="small" @click="handleEdit(scope.row)"
          >编辑</el-button
          >
          <el-popconfirm title="确定删除吗?" @confirm="handleDelete(scope.row.id)">
            <template #reference>
              <el-button type="text" size="small">删除</el-button>
            </template>
          </el-popconfirm>
        </template>
      </el-table-column>
    </el-table>
    <div style="margin: 10px 0">
      <el-pagination
          v-model:currentPage="currentPage"
          v-model:page-size="pageSize"
          :page-sizes="[5, 10, 15, 20]"
          :small="0"
          :disabled="false"
          :background="true"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
      />
    </div>
    <el-dialog
        v-model="dialogVisible"
        title="编辑"
        width="30%"
    >
      <el-form :model="form" label-width="120px">
        <el-form-item label="厂商名称" style="width: 80%">
          <el-input v-model="form.name" />
        </el-form-item>
        <el-form-item label="厂商电话" style="width: 80%">
          <el-input v-model="form.telephone" />
        </el-form-item>
        <el-form-item label="网址" style="width: 80%">
          <el-input v-model="form.web" />
        </el-form-item>
      </el-form>
      <template #footer>
      <span class="dialog-footer">
        <el-button @click="dialogVisible = false">取消</el-button>
        <el-button type="primary" @click="save"
        >确定</el-button
        >
      </span>
      </template>
    </el-dialog>
    <el-dialog
        v-model="dialogVisible2"
        title="新增厂商"
        width="30%"
    >
      <el-form :model="form" label-width="120px">
        <el-form-item label="厂商名称" style="width: 80%">
          <el-input v-model="form.name" />
        </el-form-item>
        <el-form-item label="厂商电话" style="width: 80%">
          <el-input v-model="form.telephone" />
        </el-form-item>
        <el-form-item label="厂商地址" style="width: 80%">
          <el-input v-model="form.companyAddress" />
        </el-form-item>
        <el-form-item label="工场地址" style="width: 80%">
          <el-input v-model="form.factoryAddress" />
        </el-form-item>
        <el-form-item label="网址" style="width: 80%">
          <el-input v-model="form.web" />
        </el-form-item>
        <el-form-item label="法人代表" style="width: 80%">
          <el-input v-model="form.legalrepresentative" />
        </el-form-item>
      </el-form>
      <template #footer>
      <span class="dialog-footer">
        <el-button @click="dialogVisible = false">取消</el-button>
        <el-button type="primary" @click="save"
        >确定</el-button
        >
      </span>
      </template>
    </el-dialog>
  </div>
</template>

<script>
import request from "@/utils/request";
export default {
  // eslint-disable-next-line vue/multi-word-component-names
  name: "Manufacturer",
  data(){
    return{
      form:{},
      dialogVisible:false,
      dialogVisible2:false,
      currentPage:1,
      pageSize:10,
      total:10,
      search:'',
      tableData:[]
    }
  },
  created() {
    this.load()
  },
  methods:{
    load(){
      request.post("/manufacturer/queryByLimit?pageNum="+this.currentPage+"&pageSize="+this.pageSize+"&search="+this.search
          // pageNum:this.currentPage,
          // pageSize:this.pageSize,
          // search:this.search
      ).then(res=>{
        this.tableData=res.data.records
        this.total=res.data.total
      })
    },
    add(){
      this.dialogVisible=true,
          this.form={}
    },
    save(){
      if(this.form.id){
        request.put("/manufacturer",this.form).then(res => {
          console.log(res)
          if(res.code==="0"){
            this.$message({
              type:"success",
              message:"更新成功"
            })
          }else {
            this.$message({
              type:"error",
              message:res.msg
            })
          }
          this.load()
          this.dialogVisible=false
        })
      }else{
        request.post("/manufacturer",this.form).then(res => {
          console.log(res)
          if(res.code==="0"){
            this.$message({
              type:"success",
              message:"新增成功"
            })
          }else {
            this.$message({
              type:"error",
              message:res.msg
            })
          }
          this.load()
          this.dialogVisible2=false
        })
      }
    },
    // eslint-disable-next-line no-unused-vars
    handleEdit(row){
      this.form = JSON.parse(JSON.stringify(row))
      this.dialogVisible = true
    },
    handleEdit2(){
      this.dialogVisible2 = true
    },
    handleDelete(id){
      request.delete("/manufacturer/"+id).then(res=>{
        if(res.code==="0"){
          this.$message({
            type:"success",
            message:"删除成功"
          })
        }else {
          this.$message({
            type:"error",
            message:res.msg
          })
        }
        this.load()
      })
    },
    handleSizeChange(pageSize){
      this.pageSize=pageSize
      this.load()
    },
    handleCurrentChange(pageNum){
      this.currentPage=pageNum
      this.load()
    }
  }
}
</script>

